import { createSlice } from '@reduxjs/toolkit';
import { authService, UserLogin } from '../services/authService';
import { ReducerActionType } from './types/reducerType';

const loginInLocalStorage = authService.getUserInLocalStorange()

const initialState: UserLogin = {
  userName: loginInLocalStorage
    ? loginInLocalStorage.userName
    : null,
  token: loginInLocalStorage ? loginInLocalStorage.token : null,
  refreshToken: loginInLocalStorage
    ? loginInLocalStorage.refreshToken
    : null,
  redirectLoginPageUri: '/login',
};

const authenticatedSlice = createSlice({
  name: 'authenticated',
  initialState,
  reducers: {
    loginAdded: {
      reducer(state: UserLogin, action: ReducerActionType<UserLogin>) {
        if (!initialState.token) {
          const userInfos = {
            ...action.payload,
            userName: authService.getUserNameInToken(
              action.payload.token as string,
            ),
          };
          authService.cleanUserInLocalStorange();
          authService.saveLoginInLocalStorage(userInfos);
          state.userName = userInfos.userName;
          state.token = action.payload.token;
        }
      },
      prepare(params: { token: string; refreshToken: string }): {
        payload: UserLogin;
      } {
        return {
          payload: {
            token: params.token,
            refreshToken: params.refreshToken,
          },
        };
      },
    },
    validateToken: {
      reducer(state: UserLogin, action: any) {
        if (authService.isValidToken(action.payload)) {
          return;
        }
        state.token = null;
        authService.cleanUserInLocalStorange();
      },
      prepare(params) {
        return { payload: params };
      },
    },
  },
});

export const { loginAdded, validateToken } = authenticatedSlice.actions;

export default authenticatedSlice.reducer;
